<template>
  <div class="vx-row">
    <div class="vx-col w-full md:w-1/1 lg:w-1/1 xl:w-1/1">
      <div class="flex my-4">
        <panel title="ติดต่อ">
          <template v-slot:header-right>
            <vs-button @click="openAddContact()" color="primary" type="filled"
              >เพิ่ม</vs-button
            >
          </template>
          <div class="mt-5">
            <div>
              <vs-table max-items="3" pagination search :data="tables">
                <template slot="header">
                  <h3 class="px-2">ข้อมูลติดต่อ</h3>
                </template>
                <template slot="thead">
                  <vs-th> ลำดับ</vs-th>
                  <vs-th> ข้อมูลติดต่อ </vs-th>
                  <vs-th> ประเภท </vs-th>
                  <vs-th> </vs-th>
                </template>

                <template slot-scope="{ data }">
                  <vs-tr :key="indextr" v-for="(table, indextr) in data">
                    <vs-td :data="indextr">
                      {{ indextr + 1 }}
                    </vs-td>
                    <vs-td :data="table.value">
                      {{ table.value }}
                    </vs-td>
                    <vs-td :data="table.customer_contacts">
                      {{ table.type }}
                    </vs-td>
                    <vs-td>
                      <div class="flex mb-3" align="center">
                        <div class="w-1/6 bg-grid-color h-12">
                          <vs-button
                            @click="openContact(table.id)"
                            delay="10s"
                            color="primary"
                            type="flat"
                            icon-pack="feather"
                            icon="icon-edit"
                          ></vs-button>
                        </div>
                        <div class="w-1/6 bg-grid-color h-12">
                          <vs-button
                            @click="deleteContact(table)"
                            color="danger"
                            type="flat"
                            icon-pack="feather"
                            icon="icon-trash"
                          ></vs-button>
                        </div>
                        <div class="w-1/6 bg-grid-color-secondary h-12"></div>
                      </div>
                    </vs-td>
                  </vs-tr>
                </template>
              </vs-table>
            </div>
          </div>
        </panel>
      </div>
    </div>
    <vs-popup
      classContent="popup-example"
      title="แก้ไขข้อมูลติดต่อ"
      :active.sync="popupEditContact"
    >
      <div class="flex mb-4" align="center">
        <div class="w-full bg-grid-color h-12">
          <form-input
            label="ข้อมูลติดต่อ"
            v-model="edits.value"
            v-validate="`required`"
            :col="12"
          >
          </form-input>
        </div>
        &nbsp;&nbsp;
        <div class="w-full bg-grid-color h-12">
          <form-input
            label="ประเภท"
            v-model="edits.type"
            v-validate="`required`"
            :col="12"
          >
          </form-input>
        </div>
      </div>
      <br>
      <div class="vx-row">
        <div class="flex mb-4"></div>
      </div>
      <div class="vx-row">
        <div class="vx-col sm:w-2/3 w-full ml-auto" align="right">
          <vs-button
            color="primary"
            size="medium"
            type="filled"
            @click="updateContact()"
            >ยืนยัน</vs-button
          >&nbsp;
          <vs-button
            color="danger"
            size="medium"
            type="filled"
            @click="popupEditContact = false"
            >ยกเลิก</vs-button
          >
        </div>
      </div>
    </vs-popup>
    <vs-popup
      classContent="popup-example"
      title="เพิ่ม ช่องทางการติดต่อ"
      :active.sync="popupAddContact"
    >
      <div class="flex mb-4" align="center">
        <div class="w-full bg-grid-color h-12">
          <form-input
            label="ช่องทางการติดต่อ:"
            v-model="add.value"
            v-validate="`required`"
            :col="12"
          ></form-input>
        </div>
        &nbsp;&nbsp;
        <div class="w-full bg-grid-color h-12">
          <form-input
            label="ประเภท:"
            v-model="add.type"
            v-validate="`required`"
            :col="12"
          ></form-input>
        </div>
      </div>
      <div class="vx-row">
        <div class="flex mb-4"></div>
      </div>
      <div class="vx-row">
        <div class="vx-col sm:w-2/3 w-full ml-auto" align="right">
          <vs-button
            color="primary"
            size="medium"
            type="filled"
            @click="insertContact()"
            >ยืนยัน</vs-button
          >&nbsp;
          <vs-button
            color="danger"
            size="medium"
            type="filled"
            @click="popupAddContact = false"
            >ยกเลิก</vs-button
          >
        </div>
      </div>
    </vs-popup>
  </div>
</template>

<script>

export default {
  name:'EditContactTable',
  props:{
    id:{
      default:'',
      type: String
    }
  },
  data () {
    return {
      data_id: '',
      data_model: 'App\\Models\\CustomerContact',
      popupAddContact: false,
      popupEditContact: false,
      tables: [],
      edits: {},
      add: {
        value: '',
        type: ''
      }
    }
  },
  methods: {
    async openAddContact () {
      this.popupAddContact = true
    },
    async openContact (table) {
      try {
        const {
          data: { data: Edit }
        } = await this.$http.get(
          `/api/customercontacts/${this.id}/${table}`
        )
        this.edits.id = Edit.id
        this.edits.customer_id = Edit.customer_id
        this.edits.value = Edit.value
        this.edits.type = Edit.type
        if (Edit.length === 0) {
          console.log('empty edit Table')
        }
      } catch (error) {
        throw error
      }
      this.popupEditContact = true
    },
    async loadDataTable () {
      try {
        const { data } = await this.$http.get(
          // '/api/customers'
          `/api/customers/${this.id}`
        )
        this.tables = data.data.customer_contacts
        if (this.tables.length === 0) {
          console.log('empty Contacts')
        }
      } catch (error) {
        throw error
      }
    },
    async acceptAlertFail () {
      this.$vs.notify({
        color: 'danger',
        title: 'แก้ไขข้อมูลไม่สำเร็จ',
        text: 'แก้ไขข้อมูลสมาชิกไม่สำเร็จ'
      })
    },
    async updateAlert () {
      await this.$http.patch(
        `/api/customercontacts/${this.id}/${this.edits.id}`,
        this.edits
      )
      this.loadDataTable()
      this.$vs.notify({
        color: 'success',
        title: 'แก้ไขข้อมูลสำเร็จ',
        text: 'แก้ไขข้อมูลสมาชิกสำเร็จ'
      })
    },
    async updateContact () {
      try {
        this.popupEditContact = false
        this.$vs.dialog({
          type: 'confirm',
          color: 'success',
          title: 'ต้องการเพิ่มข้อมูลหรือไม่',
          text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
          acceptText: 'ยืนยัน',
          cancelText: 'ยกเลิก',
          accept: this.updateAlert
        })
      } catch (error) {
        this.$vs.dialog({
          color: 'danger',
          title: 'เพิ่มข้อมูลไม่สำเร็จ',
          text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
          acceptText: 'ปิด'
        })
        this.popupEditContact = false
      }
    },
    deleteContact (table) {
      this.tableDelete = table
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: 'ต้องการลบรายการนี้หรือไม่ ?',
        acceptText: 'ยืนยัน',
        cancelText: 'ยกเลิก',
        text: `คุณแน่ใจหรือไม่ว่าต้องการลบ ${this.tableDelete.value}`,
        accept: this.deleteAlert
      })
    },
    async deleteAlert () {
      try {
        await this.$http.delete(
          `/api/customercontacts/${this.tableDelete.id}/${this.tableDelete.customer_id}`
        )
        await this.loadDataTable()
      } catch (error) {
        // this.$swal('ลบข้อมูลไม่สำเร็จ', '', 'error')
      }
      this.$vs.notify({
        color: 'success',
        title: 'ลบสำเร็จ',
        text: 'บันทึกข้อมูล'
      })
    },
    async acceptAlertAddContact () {
      await this.$http.post(`/api/customercontacts/${this.id}`, {
        customer_id: this.id,
        insert_value: this.add.value,
        insert_type: this.add.type
      })
      this.loadDataTable()
      this.$vs.notify({
        color: 'success',
        title: 'เพิ่มข้อมูลสำเร็จ',
        text: 'เพิ่มข้อมูลสมาชิกสำเร็จ'
      })
    },
    async insertContact () {
      try {
        this.popupAddContact = false
        this.$vs.dialog({
          type: 'confirm',
          color: 'success',
          title: 'ต้องการเพิ่มข้อมูลหรือไม่',
          text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
          acceptText: 'ยืนยัน',
          cancelText: 'ยกเลิก',
          accept: this.acceptAlertAddContact
        })
        this.loadDataTable()
      } catch (error) {
        this.popupAddContact = false
        this.$vs.dialog({
          color: 'danger',
          title: 'เพิ่มข้อมูลไม่สำเร็จ',
          text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
          acceptText: 'ปิด'
        })
      }
    }
  },
  mounted () {
    this.loadDataTable()
  }
}
</script>
