<template>
  <div>
    <div class="vx-row">
      <div class="vx-col w-full md:w-1/1 lg:w-1/1 xl:w-1/1">
        <panel title="แก้ไขข้อมูลสมาชิก">
          <br />
          <p class="ml-5">แก้ไขข้อมูลสมาชิก</p>
          <br />
          <div class="flex mb-4 ml-5">
            <form-input
              label="ชื่อ-นามสกุล"
              v-model="form.customer_name"
              v-validate="`required`"
              :col="3"
            >
            </form-input
            >&nbsp; &nbsp;
            <form-input
              label="ที่อยู่"
              v-model="form.customer_address"
              v-validate="`required`"
              :col="3"
            >
            </form-input>
            &nbsp; &nbsp;
            <form-input
              label="สาขา"
              v-model="form.customer_branch"
              v-validate="`required`"
              :col="3"
            >
            </form-input>
            &nbsp; &nbsp;

            <custom-tree-select
              class="my-5"
              url="/api/setting/master/customer"
              :col="4"
              v-model="form.setting_master_customer_id"
            >
            </custom-tree-select>
          </div>
          <p class="ml-5">แนบไฟล์เอกสาร</p>
          <div class="flex mb-4">
            <div>
              <attached
                :id="data_id"
                :model="data_model"
                ref="upload"
              ></attached>
            </div>
          </div>
          <div class="vx-row">
            <div class="vx-col sm:w-2/3 w-full ml-auto" align="right">
              <vs-button color="primary" type="filled" @click="updateCustomer()"
                >ยืนยัน</vs-button
              >&nbsp;
              <vs-button color="danger" type="filled" to="/member/index"
                >ยกเลิก</vs-button
              >
            </div>
          </div>
          {{form.setting_id}}
        </panel>
        <!-- Contact Table -->
        <EditContactTable :id="form.id" v-if="form.id"></EditContactTable>
        <!-- Close Contact Table -->
      </div>
    </div>
  </div>
</template>

<script>
import EditContactTable from './EditContactTable'
export default {
  components: {
    EditContactTable
  },
  data () {
    return {
      data_id: '',
      data_model: 'App\\Models\\Customer',
      popupAddContact: false,
      popupEditContact: false,
      form: {
        id: '',
        code: '12345',
        customer_name: '',
        customer_address: '',
        customer_contacts: '',
        customer_branch: '',
        setting_master_customer_id: '',
        type: ''
      },
      tables: [],
      edits: {},
      add: {
        value: '',
        type: ''
      }
    }
  },

  methods: {
    async loadData () {
      try {
        const {
          data: { data: CUSTOMER }
        } = await this.$http.get(`/api/customers/${this.form.id}`)
        this.form.id = CUSTOMER.id
        this.form.code = CUSTOMER.code
        this.form.customer_name = CUSTOMER.customer_name
        this.form.customer_address = CUSTOMER.customer_address
        this.form.setting_master_customer_id = CUSTOMER.setting_master_customer_id
        if (CUSTOMER.customer_contacts[0]) {
          this.form.customer_contacts = CUSTOMER.customer_contacts[0].value
        }
        // this.form.customer_id = CUSTOMER.customer_contacts[0].customer_id
        this.form.customer_branch = CUSTOMER.customer_branch
        // this.form.type = CUSTOMER.customer_contacts[0].type
      } catch (error) {
        throw error
      }
    },
    async updateCustomer () {
      try {
        const result = await this.$validator.validateAll()
        if (result) {
          this.$vs.dialog({
            type: 'confirm',
            color: 'success',
            title: 'แก้ข้อมูลสำเร็จ',
            text: `แก้ไขข้อมูลสมาชิก  ${this.form.customer_name}`,
            acceptText: 'ยืนยัน',
            cancelText: 'ยกเลิก',
            accept: this.acceptAlert
          })
        } else {
          this.$vs.dialog({
            color: 'danger',
            title: 'แก้ข้อมูลไม้สำเร็จ',
            text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
            acceptText: 'ปิด',
            accept: this.acceptAlertFail
          })
        }
      } catch (error) {
        this.$vs.dialog({
          color: 'danger',
          title: 'แก้ข้อมูลไม้สำเร็จ',
          text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
          acceptText: 'ปิด',
          accept: this.acceptAlertFail
        })
      }
    },
    async acceptAlert () {
      const resp = await this.$http.patch(
        `/api/customers/${this.form.id}`,
        this.form
      )
      this.data_id = resp.data.data.id
      this.$refs.upload.id = resp.data.data.id

      const first = await this.$refs.upload.submit()
      console.log(first)
      this.$router.push({ name: 'customer_index', query: {} })
      this.$vs.notify({
        color: 'success',
        title: 'แก้ไขข้อมูล',
        text: 'แก้ไขข้อมูลสมาชิกสำเร็จ'
      })
    },
    async acceptAlertFail () {
      this.$vs.notify({
        color: 'danger',
        title: 'แก้ไขไม่สำเร็จข้อมูล',
        text: 'แก้ไขข้อมูลสมาชิกไม่สำเร็จ'
      })
    }
  },
  mounted () {
    this.form.id = this.$route.query.id
    setTimeout(() => {
      this.loadData()
    }, 0)
  }
}
</script>

